.citation-container {
  container-type: inline-size;
}

.citation {
  display: flex;
  flex-direction: column;
  align-items: stretch;
  margin: 20px 0;
  border-radius: var(--rounded);
  background: var(--background);
  overflow: hidden;
  box-shadow: var(--shadow);
}

.citation-image {
  position: relative;
  // uncomment for left-align
  // width: 90%;
  // height: auto;
  // flex-shrink: 0;
  // padding-left: 30px;
  // padding-top: 20px;
  // box-sizing: border-box;
  width: 95%;
  height: auto;
  flex-shrink: 0;
  margin: 20px auto 0 auto;
  box-sizing: border-box;
}

.citation-image img {
  width: 100%;
  height: auto;
  object-fit: contain;
  // object-position: left top;
  object-position: center top;
  display: block;
}

.citation-text {
  position: relative;
  display: inline-flex;
  flex-wrap: wrap;
  gap: 10px;
  max-width: 100%;
  height: min-content;
  padding: 20px;
  padding-left: 30px;
  text-align: left;
  overflow-wrap: break-word;
  z-index: 0;
}

.citation-title,
.citation-authors,
.citation-details,
.citation-description {
  width: 100%;
}

.citation-title {
  font-weight: var(--semi-bold);
}

.citation-text > .icon {
  position: absolute;
  top: 20px;
  right: 20px;
  color: var(--light-gray);
  opacity: 0.5;
  font-size: 30px;
  z-index: -1;
}

.citation-publisher {
  text-transform: capitalize;
}

.citation-description {
  color: var(--gray);
}

.citation-buttons {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
}

.citation-buttons .button {
  margin: 0;
}

.citation-text > .tags {
  display: inline-flex;
  justify-content: flex-start;
  margin: 0;
}

